import { Component, Input } from '@angular/core';

import { ModalController } from 'ionic-angular';

import { LoopBackConfig } from '../../../shared/sdk';

import { ImageFileServiceProvider } from '../../providers/image-file-service/image-file-service';
import { ImageServiceProvider } from '../../providers/image-service/image-service';
import { ConfigServiceProvider } from '../../providers/config-service/config-service';
import { SharedServiceProvider } from '../../providers/shared-service/shared-service';
import { PdaparamsServiceProvider } from '../../providers/pdaparams-service/pdaparams-service';
import { SiteconfigServiceProvider } from '../../providers/siteconfig-service/siteconfig-service';

//import { ImageTextPage } from '../../pages/image-text/image-text';

/**
 * Generated class for the CameraButtonComponent component.
 *
 * See https://angular.io/docs/ts/latest/api/core/index/ComponentMetadata-class.html
 * for more info on Angular Components.
 */
@Component({
  selector: 'camera-button',
  templateUrl: 'camera-button.html'
})
export class CameraButtonComponent {
  @Input('metadata') metadata: { basejob: '', legid: '', legStatus: '' };
  imageURI;
  legid;
  pdaImageNotes;

  constructor(public modalCtrl: ModalController, private imageFileService: ImageFileServiceProvider,
    private imageService: ImageServiceProvider, private configService: ConfigServiceProvider,
    private sharedService: SharedServiceProvider, private pdaParams: PdaparamsServiceProvider,
    private siteConfig: SiteconfigServiceProvider) {
    //console.log('Hello CameraButtonComponent Component');
    LoopBackConfig.setBaseURL(configService.apiURL);
    LoopBackConfig.setApiVersion(configService.API_VERSION);

    // default is must enter notes for image but can be overridden by having site config set to 'N'
    this.pdaImageNotes = this.pdaParams.pda_image_notes || true;
    if (this.siteConfig.getSiteConfigValue('PDA_IMAGE_NOTES') === 'N')
      this.pdaImageNotes = false;
    //alert("this.pdaImageNotes from constructor of camera-button.ts:::" + this.pdaImageNotes);
  }

  // function to open the modal
  openModal = (obj) => {
    let myModal = this.modalCtrl.create("ImageTextPage", obj);
    myModal.present();
  };

  getNotesForImage = (imageURI, legid, baseFileName, newFileName) => {
    let obj = { imageURI: '', legid: 0, baseFileName: '', newFileName: '' };
    obj.imageURI = imageURI;
    obj.legid = legid;
    obj.baseFileName = baseFileName;
    obj.newFileName = newFileName;
    this.openModal(obj);
  }

  ionViewWillEnter() {
    //set the tab string in shared-service
    this.sharedService.tab = "CameraButtonComponent";
  }

  takephoto = (legid) => {
    //console.log(new Date().toISOString() + ': camera.takephoto: starts');		//DEBUG

    this.imageURI = "Nothing Loaded!!";
    this.legid = legid;
    this.imageService.takePhoto(this.legid).subscribe((imageURI) => {

      this.imageURI = imageURI;
      this.imageService.storeImageURI(this.legid, this.imageURI, this.metadata).subscribe((fileData) => {
        // image moved to app storage
        this.sharedService.imageSaved.next(fileData);
        var savedImageURI = fileData.savedImageURI;
        var baseFileName = fileData.baseFileName;
        var newFileName = fileData.newFileName;
        //alert("this.pdaImageNotes from inside takephoto:::" + this.pdaImageNotes);
        if (this.pdaImageNotes) {
          // get the notes associated with this image
          this.getNotesForImage(savedImageURI, this.legid, baseFileName, newFileName);
        } else {
          // no notes, simply mark as ready for upload
          this.imageFileService.set(newFileName, 'readyForUpload', true);
        }

        //Set the shared variable multidel_phototaken to true;
        this.sharedService.multidel_phototaken = true;
      }, (err) => {
        alert("storeImageURI Error" + JSON.stringify(err));
      });
    });
    // image successfully captured by device
  }//takephoto ends here

}



